
const state = {
    moneys: [
        { value: 10 },
        { value: 20 },
        { value: 50 },
        { value: 100 },
        { value: 200 },
    ],
    payment: {
        money: 0,
        product: [

        ]
    },
    result: "Vásárolj valamit"
}

const getters = {
    moneyValues: (state) => state.moneys,
    getRes: (state) => state.result,
    getMoney: (state) => state.payment.money,
    getAddedProducts: (state) => state.payment.product
}

const actions = {
    setValues({ commit }, value) {
        commit("setMoney", value)
    },
    setProduct({ commit }, product) {
        commit("setProduct", product)
    },
    pay({commit}) {
        let productsArray =  state.payment.product
        let money = state.payment.money;
        let sum = 0;
        for (let i = 0; i <productsArray.length; i++) {
           sum += productsArray[i].Price 
        }
        let diff = money - sum
        let res;
        if (diff >= 0) {
            res = "Sikeresen vásárolva a visszajáró: " + diff
            commit("moneyReset")
            commit("productReset")
        }
        else {
            res = "Dobj még be "+Math.abs(diff)+"ft-ot"
        }
        commit("setPayRes", res) 
       
    },

    deleteProduct({commit},product){
      let currentProducts = state.payment.product.filter(prd => product.Name!=prd.Name)

      commit("productDelete",currentProducts)
    }
}

const mutations = {
    setMoney: (state, value) => (state.payment.money += value),
    moneyReset: (state) => (state.payment.money = 0),
    productReset: (state) => (state.payment.product = []),
    productDelete: (state,product) => (state.payment.product = product),
    setProduct: (state, product) => (state.payment.product.push(product)),
    setPayRes: (state, res) => (state.result = res)
}

export default {
    state,
    getters,
    actions,
    mutations
}
